.text-center {
  text-align: center;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Kanit";
  font-size: 1.6rem;
  background-color: #f8f9fd;
}

p {
  color: #5f571a;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  font-family: "Kanit";
  font-weight: 900;
  color: #5f571a;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 7rem;
}
.header .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
}
.header .logo img {
  margin: 1rem;
  cursor: pointer;
}

.logo img {
  height: 4rem;
}

.hamburguer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
}
.hamburguer i {
  color: #e0c93c;
  margin: 1rem;
  cursor: pointer;
}
.hamburguer .active {
  color: white;
}

.lista {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 4rem;
  position: absolute;
  background-color: #f8f9fd;
  padding: 0;
  margin: 0;
  color: #f8f9fd;
  top: 7rem;
  left: -100rem;
  transition: all 0.3s ease-in-out;
  width: 100%;
  height: calc(100vh - 7rem);
}
.lista li {
  list-style: none;
  cursor: pointer;
  color: #e0c93c;
  font-size: 2rem;
  font-weight: 400;
  text-transform: uppercase;
  padding: 1rem;
  text-align: center;
}
.lista li:hover {
  background-color: #5f571a;
  color: #f8f9fd;
}

.mostrar {
  left: 0;
  transition: all 0.3s ease-in-out;
}

@media (min-width: 1024px) {
  .hamburguer {
    display: none;
  }

  .lista {
    background-color: #020808;
    flex-direction: row;
    left: 0;
    position: static;
    width: 50%;
    height: 100%;
    background-color: transparent;
  }
  .lista ul {
    display: flex;
    gap: 2rem;
    padding: 0;
    width: 60%;
    justify-content: center;
  }
  .lista ul li {
    font-size: 2rem;
  }
}
.front--div {
  width: 100%;
  height: calc(100vh - 7rem);
  padding: 2rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}
.front--div .front--div--text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.5rem;
}
.front--div .front--div--text h1 {
  font-weight: 700;
  text-transform: uppercase;
  color: #e0c93c;
  font-size: 7rem;
  margin: 0;
}
.front--div .front--div--text h2 {
  color: #e0c93c;
  font-size: 1.5rem;
  margin: 0;
  font-weight: 400;
}
.front--div .front--div--img {
  height: 45rem;
  bottom: 0;
  margin-bottom: 3rem;
}
.front--div .front--div--img img {
  height: 100%;
}
.front--div .iconos {
  color: #ad9d4a;
}

@media (min-width: 765px) {
  .front--div {
    flex-direction: row;
  }
  .front--div .front--div--img {
    display: flex;
    justify-content: center;
    height: 65rem;
    width: 50%;
  }
  .front--div .front--div--text {
    width: 50%;
    height: 100%;
    align-items: center;
  }
  .front--div .front--div--text h1 {
    font-size: 15rem;
  }
}